.gallery {
  width: 100%;
}

.gallery__wrapper {
  box-sizing: border-box;
  padding: 0;
  margin: 0 auto;
}

.gallery__header {
  box-sizing: border-box;
  padding: 0 20px;
  margin: 0 auto;
  margin-top: 29px;
}

.gallery__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: @black;
  text-transform: uppercase;
  padding: 10px 5px;
  text-align: center;
  margin: 0;
}

.gallery__description {
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: @gray;
  text-transform: uppercase;
  padding: 14px 5px;
  text-align: center;
  margin: 0;
  margin-bottom: 46px;
}

.gallery__item {
  min-height: 375px;
  margin-bottom: 49px;
  position: relative;
}

.gallery__item:nth-last-child(1) {
  margin-bottom: 32px;
}

.gallery__item::before {
  content: "";
  border: 1px #e5e5e5 solid;
  width: 100%;
  position: absolute;
  top: -24px;
  right: 0;
  left: 0;
}

.gallery__item:last-of-type::after {
  content: "";
  border: 1px #e5e5e5 solid;
  width: 100%;
  position: absolute;
  top: 399px;
  right: 0;
  left: 0;
}

.gallery__photo {
  width: 280px;
  height: 280px;
  display: block;
  object-fit: cover;
  margin: 0 auto;
}

.gallery__photo-title {
  font-size: 14px;
  line-height: 21px;
  font-weight: 700;
  color: @black;
  text-transform: uppercase;
  padding: 17px 10px 0 21px;
  margin: 0;
}

.gallery__photo-autor {
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  color: @gray;
  padding: 0 10px 0 21px;
  margin: 0;
}

.gallery__like {
  border: none;
  background-color: transparent;
  padding: 10px 10px 0 21px;
}

.gallery__counter {
  font-size: 14px;
  line-height: 21px;
  font-weight: 700;
  color: @black;
}

@media (min-width: @tablet-width) {
  .gallery {
    width: 100%;
  }

  .gallery__wrapper {
    box-sizing: border-box;
    width: 648px;
    padding: 0;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }

  .gallery__header {
    width: 648px;
    padding: 0;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .gallery__title {
    font-size: 30px;
    line-height: 26px;
    font-weight: 700;
    color: @black;
    text-transform: uppercase;
    padding: 10px 95px;
    text-align: center;
    margin: 0;
    margin-top: 23px;
  }

  .gallery__description {
    font-size: 14px;
    line-height: 21px;
    font-weight: 400;
    color: @gray;
    text-transform: uppercase;
    padding: 20px 101px;
    text-align: center;
    margin: 0;
    margin-bottom: 60px;
  }

  .gallery__item {
    width: 324px;
    min-height: 347px;
    background-color: @light-gray;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: baseline;
    padding: 0;
    position: relative;
    margin-bottom: 0;
  }

  .gallery__item:nth-of-type(2n) {
    background-color: #eaeaea;
  }

  .gallery__item::before {
    display: none;
  }

  .gallery__item--big {
    width: 648px;
    min-height: 480px;
    background-color: @light-gray;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: baseline;
    padding: 0;
    position: relative;
  }

  .gallery__photo-description {
    position: absolute;
    content: "";
    bottom: 24px;
    right: 16px;
  }

  .gallery__item:last-of-type {
    margin-right: auto;
  }

  .gallery__item:last-of-type::after {
    display: none;
  }

  .gallery__photo {
    width: 100%;
    height: 267px;
    display: block;
    object-fit: cover;
  }

  .gallery__photo--big {
    width: 648px;
    min-height: 400px;
    display: block;
    object-fit: cover;
  }

  .gallery__photo-title {
    font-size: 14px;
    line-height: 21px;
    font-weight: 700;
    color: @black;
    text-transform: uppercase;
    padding: 5px 5px 0 5px;
    margin: 17px 10px 0 15px;
  }

  .gallery__photo-autor {
    font-size: 14px;
    line-height: 21px;
    font-weight: 400;
    color: @gray;
    padding: 0 5px 0 5px;
    margin: 0 10px 13px 15px;
  }

  .gallery__like {
    border: none;
    background-color: transparent;
  }

  .gallery__counter {
    font-size: 14px;
    line-height: 21px;
    font-weight: 700;
    color: @black;
    margin: 0;
    margin-right: 3px;
    padding: 0;
  }
}

